import React, { useState } from "react";
import { ListItem } from "../listItem/ListItem";
import { InputHero } from "../inputhero/InputHero";
import { RandomButton } from "../randombutton/RandomButton";

export const List = () => {
  const readList = () => {
    return localStorage.getItem("list")
      ? JSON.parse(localStorage.getItem("list"))
      : [
          {
            id: 1,
            name: "Pudge",
          },
          {
            id: 2,
            name: "Riki",
          },
          {
            id: 3,
            name: "Huskar",
          },
        ];
  };

  const [list, setList] = useState(readList());

  const saveList = () => {
    localStorage.setItem("list", JSON.stringify(list));
  };

  const addHero = (hero) => {
    setList([...list, { id: list.length + 1, name: hero }]);
  };

  const deleteHero = (index) => {
    setList(list.filter((item, i) => index !== i));
  };

  saveList();

  return (
    <div
      style={{ display: "flex", flexDirection: "column", alignItems: "center" }}
    >
      <ul className="list">
        {list.map((item, index) => {
          return (
            <ListItem
              key={index}
              name={item.name}
              onDeleteHero={deleteHero}
              index={index}
            />
          );
        })}
      </ul>
      <InputHero onAddHero={addHero} />
      <RandomButton list={list} />
    </div>
  );
};
