import React from "react";
import "./styles.css";

export const ListItem = (props) => {
  return (
    <div className="list-item">
      <div style={{ marginRight: "50px" }}>{props.name}</div>
      <button
        onClick={(event) => {
          event.preventDefault();
          props.onDeleteHero(props.index);
        }}
      >
        X
      </button>
    </div>
  );
};
