import React, { useState } from "react";

export const RandomButton = (props) => {
  const [randomHero, setRandomHero] = useState("");

  const random = () => {
    const elem = Math.floor(Math.random() * props.list.length);
    setRandomHero(props.list[elem]);
  };

  return (
    <div>
      <button onClick={random}>Random Button</button>
      <p>random hero: {randomHero.name}</p>
    </div>
  );
};
